import Vue from 'vue'
import axios from 'axios'


const API_URL = process.env.VUE_APP_BASE_API_URL;

const plainAxiosInstance = axios.create({
  baseURL: `${API_URL}`,
  timeout: 1000,
  headers: {
    'Content-Type': 'application/json'
  }
})

const securedAxiosInstance = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json'
  }
})

securedAxiosInstance.interceptors.response.use(function (response) {
  // Any status code that lie within the range of 2xx cause this function to trigger
  // Do something with response data
  return response;
}, function (error) {

  plainAxiosInstance.get('/auth/validate_token',
    { headers: {
        'Content-Type': 'application/json' ,
        'uid': localStorage.getItem('user_email'),
        'client': localStorage.getItem('client'),
        'access-token': localStorage.getItem('access-token')
      }
    })

    .then((result) => {
      localStorage.set('user_email', result.headers.uid)
      localStorage.set('access-token', result.headers['access-token'])
      localStorage.set('client', result.headers.client)
      localStorage.set('expiry', result.headers.expiry)

    }).catch((err) => {

      console.log( err )
      localStorage.removeItem('user_email')
      localStorage.removeItem('access-token')
      localStorage.removeItem('client')
      localStorage.removeItem('expiry')
      localStorage.removeItem('access_token')
      localStorage.removeItem('created_at')
      localStorage.removeItem('expires_day')
      localStorage.removeItem('expires_hour')
      localStorage.removeItem('expires_minute')
      localStorage.removeItem('token_type')
      location.replace('/')
    });
  return Promise.reject(error);
});


Vue.prototype.$axios = plainAxiosInstance
Vue.prototype.$axios = securedAxiosInstance

export { plainAxiosInstance, securedAxiosInstance }
