<template>

  <v-app>
    <default-app-bar v-if="!logged"/>

    <core-app-bar v-if="logged"/>

    <core-drawer v-if="logged"/>

    <core-view />

  </v-app>
</template>

<script>
  export default {
    components: {
      CoreDrawer: () => import('@/components/core/Drawer'),
      CoreAppBar: () => import('@/components/core/AppBar'),
      CoreView: () => import('@/components/core/View'),
      DefaultAppBar: () => import('@/components/core/AppBarExternal')
    },

    data() {
      return {
        logged: false
      }
    },

    mounted() {
      let today = new Date();
      if ( parseInt(today.getDate()) <= parseInt(localStorage.getItem( 'expires_day' ))) {

        if ( parseInt(today.getHours()) < parseInt(localStorage.getItem( 'expires_hour' )) ) {
          this.logged = true
          this.$router.push('/admin/dashboard')

        } else if (
                  parseInt(today.getHours()) == parseInt(localStorage.getItem( 'expires_hour' )) &&
                  parseInt(today.getMinutes()) < parseInt(localStorage.getItem( 'expires_minutes' ))
                )
        {
          this.logged = true
          this.$router.push('/admin/dashboard')

        }
      }

      if ( !this.logged ) { this.$router.push('/') }
    },
  }
</script>
